<template>
  <div class="js-scenarist-child">
    <div class="layout">
      <vx-header :showback="1" @back="back" :lazy="1" :title="$t('common.windowLayout')" @default="reset"></vx-header>
      <div class="vx-container">
        <div class="list bm">
          <div class="title">{{$t('common.width')}}</div>
          <div class="adds">
            <vx-addminus :label="$t('common.widthl') + $t('common.rangesfrom')" :num="width.value" :regex="'^[1-9]*[1-9][0-9]*$'" :max="width.max" :min="width.min" @touchend="save" @update="val=>width.value=val"></vx-addminus>
          </div>
        </div>
        <div class="list bm">
          <div class="title">{{$t('common.height')}}</div>
          <div class="adds">
            <vx-addminus :label="$t('common.heightl') + $t('common.rangesfrom')" :num="height.value" :regex="'^[1-9]*[1-9][0-9]*$'" :max="height.max" :min="height.min" @touchend="save" @update="val=>height.value=val"></vx-addminus>
          </div>
        </div>
        <div class="list bm">
          <div class="title">{{$t('common.X')}}</div>
          <div class="adds">
            <vx-addminus :label="$t('common.Xl') + $t('common.rangesfrom')" :num="x.value" :regex="'^-?\\d+$'" :max="x.max" :min="x.min" @touchend="save" @update="val=>x.value=val"></vx-addminus>
          </div>
        </div>
        <div class="list">
          <div class="title">{{$t('common.Y')}}</div>
          <div class="adds">
            <vx-addminus :label="$t('common.Yl') + $t('common.rangesfrom')" :num="y.value" :regex="'^-?\\d+$'" :max="y.max" :min="y.min" @touchend="save" @update="val=>y.value=val"></vx-addminus>
          </div>
        </div>
        <div class="button-box">
          <button class="right-text  yellow-btn" @click="reset">{{$t('common.default')}}</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { serialize } from 'src/utils';
  import htp from "../../api/http";
  import url from '../../api/apiurl';
  import { Toast } from 'mint-ui';
  import { setTimeout } from 'timers';
  export default {
    data() {
      return {
        width: {
          value: 64,
          max: 32767,
          min: 64
        },
        height: {
          value: 64,
          max: 32767,
          min: 64
        },
        x: {
          value: 0,
          max: 32767,
          min: -32767
        },
        y: {
          value: 0,
          max: 32767,
          min: -32767
        },
        priority: '',
        canCom: true
      };
    },
    created() {
      htp.post(url.getWinBaseInfo, { selectWindow: this.$route.params.winId })
        .then(res => {
          let data = res.data;
          console.log(data);
          if(res.status === 10000000) {
            this.width.value = data.width;
            this.height.value = data.height;
            this.x.value = data.startX;
            this.y.value = data.startY;
            this.priority = data.priority;
          }
        });
    },
    methods: {
      reset() {
        this.width.value = 800;
        this.height.value = 600;
        this.x.value = (this.$route.params.winId - 1) * 100;
        this.y.value = (this.$route.params.winId - 1) * 50;
        htp.post(url.setWinParam, {
          windowStartX: Number(this.x.value),
          windowStartY: Number(this.y.value),
          windowWidth: this.width.value,
          windowHeight: this.height.value,
          windowId: this.$route.params.winId
        }).then(res => {
          if(res.status === 10000000) {
            Toast(this.$t('common.successful'));
          } else {
            Toast(this.$t('common.requestFailed'));
          }
        });
      },
      save(flag) {
        this.canCom = flag;
        htp.post(url.setWinParam, {
          windowStartX: Number(this.x.value),
          windowStartY: Number(this.y.value),
          windowWidth: this.width.value,
          windowHeight: this.height.value,
          windowId: this.$route.params.winId
        });

      },
      back() {
        // console.log(this.canCom);
        // setTimeout(() => {
        //   if(this.canCom) {
        this.$router.go(-1);
        //   } else {
        //     this.canCom = true;
        //     return false;
        //   }
        // }, 100);
      }
    }
  };
</script>

<style lang="less" scoped>
  .vx-container {
    top: 110px;
  }
  .bm {
    border-bottom: 1px solid #4f5054; /*no*/
  }
  .list {
    height: 85px;
    display: flex;
    padding: 0 35px;
    justify-content: space-around;
    background-color: #383940;
    .title {
      width: 120px;
      height: 85px;
      font-size: 34px;
      line-height: 85px;
      text-align: left;
      color: #bcbcbe;
    }
    .adds {
      flex: 1;
      height: 85px;
      position: relative;
    }
  }
  .mask {
    height: 75px;
    padding: 0 35px;
    line-height: 75px;
    font-size: 26px;
    color: #f8f8f8;
    text-align: left;
    background-color: #212124;
  }
  .button-box {
    // border-top: 1px solid #4f5054;
    width: 100%;
    height: 105px;
    display: flex;
    padding: 0 35px;
    align-items: center;
    // background-color: #383940;
    justify-content: center;
    margin-top: 14px;
    .right-text {
      height: 40px;
      font-size: 14px; /*no*/
      color: #ddd;
      padding: 10px 20px;
      height: 70%;
      background: #933e00;
      width: 75%;
      border: 1px solid #933e00;
      border: none;
      outline: none;
      border-radius: 8px;
    }
  }
</style>


